* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background-color: var(--color-bg);
    color: var(--color-text);
}

a {
    text-decoration: none;
    color: unset;
}

@media screen and (max-width: 700px) {
    .section__padding {
       padding: 4rem;
    }

    .section__margin {
       margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
       padding: 4rem 2rem;
    }

    .section__margin {
       margin: 4rem 2rem;
    }
}